<template>
	<transition
		:css="false"
		appear
		@before-enter="_before_enter"
		@enter="_enter"
		@leave="_leave"
	>
        <div class="flex-center background">
            <div style="width: 144vh;height:74%;margin-bottom: 3.4%;background-color:#fff;">
                <div class="flex-between">
                    <button
                        v-for="(tab,tabIndex) in tabs"
                        :key="tabIndex"
                        style="flex-grow:1;height:36px;"
                    >{{tab}}</button>
                </div>
                <div>
                    <my-table :cols="cols" :data="data" :tdStyle="tdStyle" :thStyle="tdStyle" @tdclick="chartVisible = !chartVisible"/>
                </div>
            </div>

            <!-- 折线弹窗 -->
        </div>
    </transition>
</template>

<script>
    import myTable from '@/components/myTable.vue';
    import animate from 'velocity-animate/velocity';
    export default {
        data:()=>({
            tabs: [
                '直流电阻', '回路电阻', '交流耐压', '回路电阻', '工频放压', '动作电压', '分合时间', '直流耐压分合时间'
            ],
            tdStyle: {
                height: '100px'
            },
            cols: [
                { name: 'c1', title: '试验项目' },
                { name: 'c2', title: 'A(B)' },
                { name: 'c3', title: 'B(C)' },
                { name: 'c4', title: 'C(A)' },
                { name: 'c5', title: '差值(%)' },
                { name: 'c6', title: '温度' },
                { name: 'c7', title: '备注' },
            ],
            data: [
                { c1: '1号主变绕组高压侧', c2: '1306', c3: '1306', c4: '1307', c5: '0.07', c6: '30', c7: '' },
                { c1: '1号主变绕组低压侧', c2: '1.414', c3: '1.414', c4: '1.418', c5: '0.28', c6: '30', c7: '' },
                { c1: '1号主变低压侧PT(一次绕组KΩ)', c2: '2.476', c3: '2.386', c4: '2.507', c5: '', c6: '21.4', c7: '' },
                { c1: '1号主变低压侧PT(二次绕组mΩ)an', c2: '168.4', c3: '173.3', c4: '167.9', c5: '', c6: '21.4', c7: '' },
                { c1: '1号主变低压侧PT(二次绕组mΩ)dadn', c2: '130.5', c3: '130.5', c4: '115.8', c5: '', c6: '21.4', c7: '' },
            ],
            chartVisible: false
        }),
        components: {
            myTable
        },
        methods:{
            _before_enter(el){
                el.style.opacity = 0;
            },
            _enter(el,done){
                animate(el,{ opacity: 1 },{ duration: 1200, complete: done });
            },
            _leave(el,done){
                animate(el,{ opacity: 0},{ duration: 600, complete: done});
            }
        }
    }
</script>

<style scoped>
    .background {
        width: 100%;
		height: 100%;
		background-image: url('../../assets/img/back2.png');
		background-position: center center;
		background-size: auto 100%;
		color: #fff;
	}
</style>