<template>
	<div style="width:100%;height:100%;background-color: #fff;color: #333;">
		<div v-if="!cols.length || !data.length" style="text-align:center;color:#999;">没有数据</div>
		<div v-else class="flex-between" style="height: 100%;overflow-y:auto;">
			<div v-for="(col,colIndex) in cols" :key="colIndex" style="flex-grow:1;">
				<div class="item flex-center" :style="thStyle">{{col.title}}</div>
				<div class="item flex-center" :style="tdStyle" v-for="(row,rowIndex) in data" :key="rowIndex" @click="$emit('tdclick',row[col.name],col)">{{row[col.name]}}</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		props:{
			cols: {
				type: Array,
				default: new Array()
			},
			data: {
				type: Array,
				default: new Array()
			},
			thStyle: {
				type: Object,
				defautl: new Object()
			},
			tdStyle: {
				type: Object,
				defautl: new Object()
			}
		}
	}
</script>

<style scoped>
	.item {
		height: 40px;
		padding: 8px;
		border-right: 2px solid #fff;
		border-bottom: 2px solid #fff;
		background-color: #e9ebf5;
	}
	.item:active {
		background-color: #b4c7e7;
	}
</style>
